import { Send, Paperclip, Mic } from 'lucide-react';

interface ChatInputProps {
  input: string;
  setInput: (value: string) => void;
  isLoading: boolean;
  onSend: () => void;
  onKeyPress: (e: React.KeyboardEvent) => void;
}

export default function ChatInput({ 
  input, 
  setInput, 
  isLoading, 
  onSend, 
  onKeyPress 
}: ChatInputProps) {
  return (
    <div className="bg-white rounded-xl border border-gray-200 shadow-sm p-2">
      <div className="flex items-center space-x-2">
        <button className="p-2 text-gray-500 hover:text-gray-700 hover:bg-gray-100 rounded-lg transition-colors">
          <Paperclip className="w-5 h-5" />
        </button>
        
        <div className="flex-1 relative">
          <textarea
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyDown={onKeyPress}
            placeholder="Type your message here..."
            className="w-full p-3 pr-12 border-0 focus:ring-0 resize-none max-h-40 min-h-[60px] bg-transparent"
            rows={1}
            disabled={isLoading}
          />
          
          <button className="absolute right-2 top-1/2 transform -translate-y-1/2 p-2 text-gray-500 hover:text-gray-700 hover:bg-gray-100 rounded-lg transition-colors">
            <Mic className="w-5 h-5" />
          </button>
        </div>
        
        <button
          onClick={onSend}
          disabled={!input.trim() || isLoading}
          className="p-3 bg-gradient-to-r from-blue-500 to-purple-600 text-white rounded-xl hover:opacity-90 disabled:opacity-50 disabled:cursor-not-allowed transition-all"
        >
          <Send className="w-5 h-5" />
        </button>
      </div>
      
      <div className="mt-2 text-xs text-gray-500 px-2">
        Press Enter to send â€¢ Shift + Enter for new line
      </div>
    </div>
  );
}