import { Bot, User, Copy, Check } from 'lucide-react';

interface ChatMessageProps {
  message: {
    id: string;
    content: string;
    role: 'user' | 'assistant';
    timestamp: Date;
  };
  isCopied: boolean;
  onCopy: () => void;
}

export default function ChatMessage({ message, isCopied, onCopy }: ChatMessageProps) {
  const isUser = message.role === 'user';
  const formattedTime = message.timestamp.toLocaleTimeString([], { 
    hour: '2-digit', 
    minute: '2-digit' 
  });

  return (
    <div className={`flex ${isUser ? 'justify-end' : 'justify-start'}`}>
      <div className={`flex max-w-[80%] ${isUser ? 'flex-row-reverse' : 'flex-row'}`}>
        {/* Avatar */}
        <div className={`flex-shrink-0 w-8 h-8 rounded-full flex items-center justify-center ${
          isUser ? 'ml-3 bg-blue-500' : 'mr-3 bg-gradient-to-r from-blue-500 to-purple-600'
        }`}>
          {isUser ? (
            <User className="w-4 h-4 text-white" />
          ) : (
            <Bot className="w-4 h-4 text-white" />
          )}
        </div>

        {/* Message bubble */}
        <div className={`relative rounded-2xl px-4 py-3 ${
          isUser 
            ? 'bg-blue-500 text-white rounded-tr-none' 
            : 'bg-white border border-gray-200 shadow-sm rounded-tl-none'
        }`}>
          <div className="flex items-center justify-between mb-1">
            <span className={`text-xs font-medium ${isUser ? 'text-blue-100' : 'text-gray-500'}`}>
              {isUser ? 'You' : 'AI Assistant'}
            </span>
            <span className={`text-xs ml-2 ${isUser ? 'text-blue-200' : 'text-gray-400'}`}>
              {formattedTime}
            </span>
          </div>
          
          <p className="whitespace-pre-wrap break-words">{message.content}</p>
          
          {/* Copy button */}
          <button
            onClick={onCopy}
            className={`absolute -bottom-2 ${isUser ? 'left-2' : 'right-2'} ${
              isUser 
                ? 'bg-blue-400 hover:bg-blue-300' 
                : 'bg-gray-100 hover:bg-gray-200'
            } p-1 rounded-full transition-colors`}
            title="Copy message"
          >
            {isCopied ? (
              <Check className="w-3 h-3 text-green-600" />
            ) : (
              <Copy className="w-3 h-3 text-gray-600" />
            )}
          </button>
        </div>
      </div>
    </div>
  );
}